<script>
let isScroll = false
let position = window.sessionStorage.getItem("position") == null ? 0 : Number(window.sessionStorage.getItem("position"))
export default {
  name: 'app',
  data () {
    return {
      words: [
        "hi, i' am",
        'billy purnama',
        'front end developer',
        'i would love to make your website looks cooler than ever.'
      ],
      descriptions: [
        'web development',
        'design & styling',
        'branding & identity',
        'UI / UX design'
      ],
      histories: [
        'education history',
        'work history'
      ],
      educationHistories: [
        {
          year: '2017',
          grade: 'iOS Foundation Program',
          instituteName: 'Apple Developer Academy',
          description: 'I enrolled at iOS Foundation Program for 1 month, and I train how to code with swift playground, and also how to use XCode to develop mobile apps.'
        },
        {
          year: '2015',
          grade: 'School of Information System',
          instituteName: 'Binus University',
          description: "I took my bachelor graduate program of School of Information System at Binus University for 4 years, and currently waiting for my thesis to come."
        },
        {
          year: '2012',
          grade: 'Senior High School',
          instituteName: 'SMA Bintang Mulia',
          description: 'I study in SMA Bintang Mulia for 3 years at social studies degree, and I decided to take my career path as a web developer since i graduated.'
        }
      ],
      workHistories: [
        {
          year: '2018',
          position: 'Freelance Front End Developer',
          instituteName: 'Self Employed',
          description: 'I currently self employed myself for being a freelance front end developer. I create a website mainly in front end design, and i love what i do.'
        },
        {
          year: '2017',
          position: 'Assistant Laboratory',
          instituteName: 'Binus University',
          description: 'I worked as an Assistant Laboratory at Binus University for 2 years. I teach students how to code and develop an application.'
        },
      ],
      skillList: [
        'development skills',
        'design skills'
      ],
      devSkills: [
        {
          skill: 'html5',
          percentage: 90
        },
        {
          skill: 'css3',
          percentage: 80
        },
        {
          skill: 'javascript',
          percentage: 70
        },
        {
          skill: 'jquery',
          percentage: 74
        },
        {
          skill: 'vue.js',
          percentage: 80
        },
        {
          skill: 'sass / scss',
          percentage: 75
        },
        {
          skill: 'php',
          percentage: 65
        },
        {
          skill: 'laravel',
          percentage: 83
        },
        {
          skill: 'mysql',
          percentage: 75
        }
      ],
      designSkills: [
        {
          skill: 'photoshop',
          percentage: 70
        },
        {
          skill: 'illustrator',
          percentage: 64
        }
      ]
    }
  },
  methods: {
    init() {
      this.setAnchorActive()
      this.setAnchor()
    },
    getElementY(query) {
      return window.pageYOffset + document.querySelector(query).getBoundingClientRect().top
    },
    doScrolling(element, duration) {
      let vm = this
      let startingY = window.pageYOffset
      let elementY = vm.getElementY(element)

      let targetY = document.body.scrollHeight - elementY < window.innerHeight ? document.body.scrollHeight - window.innerHeight : elementY
      let diff = targetY - startingY

      let easing = function (t) { return t<.5 ? 4*t*t*t : (t-1)*(2*t-2)*(2*t-2)+1 }
      let start

      if (!diff) return

      window.requestAnimationFrame(function step(timestamp) {
        if (!start) start = timestamp
        let time = timestamp - start
        let percent = Math.min(time / duration, 1)
        percent = easing(percent)

        window.scrollTo(0, startingY + diff * percent)

        if (time < duration) {
          window.requestAnimationFrame(step)
        }else {
          isScroll = false
        }

        if(Math.round(percent) == 1) {
          if(position == 1) vm.setAnchor("black")
          else vm.setAnchor("white")
        }

      })
    },
    fullScroll () {
      let vm = this
      let section = document.querySelectorAll(".section")
      window.addEventListener('wheel', (e) => {
        if(e.deltaY < 0){
          if(!isScroll && position > 0){
            isScroll = true
            vm.scrollTop(e, vm)
            position--
            vm.setAnchorActive()
          }
        }else if(e.deltaY > 0){
          if(!isScroll && position < 2){  
            isScroll = true
            vm.scrollBottom(e, vm)
            position++
            vm.setAnchorActive()
          } 
        }
      })
    },
    scrollTop (e, vm) {
      if(position == 1){
        vm.doScrolling(".section:nth-child(1)", 1200) 
      }
      else{
        vm.doScrolling(".section:nth-child(2)", 1200) 
      }
    },
    scrollBottom (e, vm) {
      if(position == 0){
        vm.doScrolling(".section:nth-child(2)", 1200)
      }
      else{
        vm.doScrolling(".section:nth-child(3)", 1200) 
      }
    },
    setAnchor () {
      let anchors = document.querySelectorAll("ul>li>a")
      anchors.forEach((e) => {
        e.children[0].classList.remove("white", "black")
        if(position == 1) e.children[0].classList.add("black")
        else e.children[0].classList.add("white")
      })
    },
    setAnchorActive () {
      let anchors = document.querySelectorAll("ul>li>a")
      document.querySelector("span.active").classList.remove("active")
      anchors.item(position).children[0].classList.add("active")
    }
  },
  mounted () {
    this.init()
    window.addEventListener("beforeunload", e => {
      window.sessionStorage.setItem("position", position)
    })
    document.querySelectorAll("div.event")[0].classList.add("current")
    document.querySelectorAll("div.event")[3].classList.add("current")
    this.fullScroll()
  }
};
</script>

