    border-right-color: #EDF1F3;
    border-width: 15px;
    margin-top: -15px;
  }
  &::after {
    border-right-color: #ffffff;
    border-width: 10px;
    margin-top: -10px;
  }
  &:last-child p::before {
    height: 76%;
  }
  & .date {
    background-color: #345;
    position: absolute;
    top: 45%;
    margin-top: -24px;
    left: -106px;
    width: 60px;
    line-height: 58px;
    text-align: center;
    display: block;
    z-index: 120;
    color: #fff;
    border-radius: 50%;
    border: 5px solid #FFF;
    font-family: Oswald;
    font-weight: 400;
    font-size: 11px;
    letter-spacing: 1px;
  }
  &.current .date {
    border-color: #FF1177;
    outline: 5px solid #FFF;
    background-color: #FF1177;
  }
  & p {
    margin: .6em 0 .2em 0;
    font-size: 13px;
    letter-spacing: .02em;
  }
  & p::before {
    display: block;
    content: "";
    position: absolute;
    top: -30px;
    left: -72px;
    height: 160%;
    width: 4px;
    background: #F4F6F7;
  }
  & h4 {
    margin: 0;
    padding: 0;
  }
  & h5 {
    margin: 0;
    padding: 0 0 .3em 0;
    font-size: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: 700;
    color: #C9D5D6;
  }
}
.date + p {
  margin: 0px;
}
.history-brand {
  display: flex;
  padding: 0 !important;
  flex-direction: row;
  & > div{
    margin: 0 30px 0 0;
  }
}
.skills-brand {
  padding-left: 0;
}
.section-title {
  position: relative;
  margin-bottom: 1em;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 700;
  &::before {
    content: " ";
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: 3px;
    width: 100%;
    border-bottom: 4px solid #F4F6F7;
  }
  & span {
    position: relative;
    display: inline-block;
    padding-right: 1.4em;
    background: #fff;
    & i {
      width: 42px !important;
    }
  }
}
.skillset {
  padding: 1.2rem 0 3rem 0 !important;
}
.skill-unit {
  padding-bottom: 0;
  & h4 {
    margin-bottom: -26px;
    font-weight: 800;
    font-size: 11px;
    letter-spacing: 1px;
    color: #fff;
    position: relative;
    z-index: 100;
    padding-left: 14px;
    text-transform: uppercase;
  }
  & .bar {
    position: relative;
    width: 100%;
    height: 36px;
  }
  & .bar .progress {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    border-radius: 4px;
    background: #FF1177;
  }
}
</style>



